<template>
  <div>
    我的
    <div class="logoutBtn">
      <van-button type="danger"
        @click="logOut"
        block>退出登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {

    };
  },
  methods: {
    logOut() {
      this.$toast.loading({
        duration: 0,
        message: '加载中...',
        loadingType: "spinner",
        forbidClick: true,
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$toast.success('退出成功');
        localStorage.removeItem('userInfo')
        this.$store.commit('removeToken', '')
        this.$router.push('/home')
      }, 1000);
    }
  },
  created() {

  },
};
</script>

<style scoped lang="less">
.logoutBtn {
  position: fixed;
  bottom: 90px;
  left: 15px;
  right: 15px;
}
</style>



