<template>
  <div>
    <Login v-if="!isLogin" />
    <User v-else />
  </div>
</template>

<script>
import Login from './components/login'
import User from './components/user'
export default {
  components: {
    Login,
    User
  },
  data() {
    return {
      isLogin: this.$store.getters.token ? true : false,
    };
  },
  methods: {},
  created() {

  }
};
</script>

<style scoped lang="less"></style>



