<template>
  <div>
    <div>
      首页
    </div>
    <p>
      只限本地测试使用
    </p>
    <van-button type="primary"
      @click="post"
      disabled>post接口测试</van-button>
    <van-button type="primary"
      @click="get"
      disabled>get接口测试</van-button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    post() {
      this.$http.home
        .testPost({
          aaa: 11
        })
        .then(res => {
          this.$toast.success('请求成功');
          console.log(res);
        });
    },
    get() {
      this.$http.home
        .testGet({
          aaa: 11
        })
        .then(res => {
          this.$toast.success('请求成功');
          console.log(res);
        });
    }
  },
  created() { }
};
</script>

<style scoped lang="less"></style>



