<template>
  <div>
    我是登录页
    <div class="loginBtn">
      <van-button type="primary"
        @click="login"
        block>登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    login() {
      this.$toast.loading({
        duration: 0,
        message: '加载中...',
        loadingType: "spinner",
        forbidClick: true,
      });
      setTimeout(() => {
        this.$toast.clear();
        let userInfo = {
          name: '张三',
          tel: '15888888888',
          token: '123456'
        }
        localStorage.setItem('userInfo', JSON.stringify(userInfo))
        this.$store.dispatch('setUserInfo', userInfo)
        this.$toast.success('登录成功');
        this.$router.push('/home')
      }, 1000);
    }
  },
  created() { }
};
</script>

<style scoped lang="less">
.loginBtn {
  position: fixed;
  bottom: 90px;
  left: 15px;
  right: 15px;
}
</style>



