<template>
  <div>
    <van-tabbar route active-color="#07c160" inactive-color="#000">
      <van-tabbar-item
        v-for="(item, index) in list"
        replace
        :to="item.path"
        :icon="item.icon"
        :key="index"
      >
        {{ item.name }}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          path: "/home",
          icon: "home-o",
          name: "首页"
        },
        {
          path: "/task",
          icon: "apps-o",
          name: "任务"
        },
        {
          path: "/running",
          icon: "logistics",
          name: "运行"
        },
        {
          path: "/repair",
          icon: "setting-o",
          name: "维护"
        },
        {
          path: "/user",
          icon: "contact",
          name: "我的"
        }
      ]
    };
  },
  methods: {}
};
</script>

<style scoped lang="less">
.van-tabbar--fixed {
  background: linear-gradient(
    0deg,
    rgba(240, 235, 235, 1),
    rgba(255, 255, 255, 1)
  );
  border-top: 1px solid #e5e5e5;
}
@media screen and (width: 375px) and (height: 812px) {
  .van-tabbar--fixed {
    padding-bottom: 34px;
  }
}
</style>



