import Vue from "vue";
import Router from "vue-router";
import { Toast } from "vant";
import store from "../store";
import NavBar from "../components/NavBar.vue";
import Tabbar from "../components/Tabbar.vue";
import register from "../view/register.vue";
import forgetPassword from "../view/forgetPassword.vue";
import home from "../view/home/index.vue";
import repair from "../view/repair/index.vue";
import running from "../view/running/index.vue";
import task from "../view/task/index.vue";
import user from "../view/user/index.vue";
import NotFound from "../view/NotFound.vue";

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/home"
    },
    {
      path: "/home",
      name: "home",
      meta: {
        title: "ColdChain",
        isShow: false
      },
      components: {
        default: home,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/repair",
      name: "repair",
      meta: {
        title: "维护",
        isShow: false
      },
      components: {
        default: repair,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/running",
      name: "running",
      meta: {
        title: "运行",
        isShow: false
      },
      components: {
        default: running,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/task",
      name: "task",
      meta: {
        title: "任务",
        isShow: false
      },
      components: {
        default: task,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/user",
      name: "user",
      meta: {
        title: "我的",
        isShow: false
      },
      components: {
        default: user,
        NavBar: NavBar,
        Tabbar: Tabbar
      }
    },
    {
      path: "/register",
      name: "register",
      component: register
    },
    {
      path: "/forgetPassword",
      name: "forgetPassword",
      component: forgetPassword
    },
    {
      path: "*",
      meta: {
        title: "404",
        isShow: true
      },
      components: {
        default: NotFound,
        NavBar: NavBar
      }
    }
  ]
});
router.beforeEach((to, from, next) => {
  if (
    to.path === "/forgetPassword" ||
    to.path === "/register" ||
    to.path === "/user" ||
    to.path === "/home"
  ) {
    next();
  } else {
    let token = store.getters.token;
    if (!token) {
      Toast("请先登录!");
      next("/user");
    } else {
      next();
    }
  }
});
export default router;



