<template>
  <div>
    <van-nav-bar :title="meta.title" @click-left="$router.go(-1)">
      <template #left v-if="meta.isShow">
        <van-icon name="arrow-left" size="18" color="#333" /> 返回
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      meta: {
        title: "ColdChain",
        isShow: true
      }
    };
  },
  watch: {
    $route(to, from) {
      this.meta = to.meta;
    }
  },
  methods: {},
  created() {
    this.meta = this.$route.meta;
  }
};
</script>

<style scoped lang="less">
/deep/ .van-nav-bar__left {
  color: #333;
}

/deep/ .van-nav-bar__title {
  font-weight: bold;
  font-size: 18px;
}
</style>



